$main-grey: #dbe7ea;
$main-grey-alt: #bad0d7;
$darkgrey: #688A93;
$main-bggrey: #ebf2f3;

$blackbg: #243235;
$grey-txt: #A4A4A4;
$white-transparent: rgba(255, 255, 255, 0.95);
$grey-transparent: rgba(91, 144, 159, 0.20);
// 5B909F 20%
$title: #E7504F;
$topmenuheight: 50px;
$lettercube: 78px;

// Fonts
@import "./fontstyles";

// Common
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrap a {
    text-decoration: none;
    font-size: 16px;
    line-height: 24px;
    color: $darkgrey;

    &:hover {
        color: black;
    }
}

body {
    // font-family: 'PT Sans', sans-serif;
    font-family: 'PTSansExpert', sans-serif;
    // background-color: $blackbg;
}

button {
    font-family: 'PTSansExpert', sans-serif;
}

.hidden {
    display: none;
    // position: absolute;
    // left: -9999px;
    // width: 1px;
    // margin: -1px;
}

.wrap_total {
    // max-width: 1270px;
    width: 100%;
    padding: 0 30px;
    background-color: $blackbg;
    margin: 0 auto;
}

.wrap {
    display: flex;
    width: 100%;
    position: relative;
    background-color: white;
}

.container {
    width: 80%;
    padding: 0 25px 0 20px;
}

.flexcontainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1px;
}

.innercontainer {
    display: flex;
    width: 100%;
}



// Top menu and footer

.wrap_topmenu {
    position: sticky;
    padding: 0 20px;
    top: 0;
    z-index: 900;
    width: 100%;
    background-color: #243235;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    height: $topmenuheight;
}

.top_menu a {
    text-decoration: none;
    font-size: 16px;
    line-height: 24px;
}

.mainmenubtn {
    border: none;
    background: transparent;
    color: $darkgrey;
    font-size: 16px;
    line-height: 24px;
    cursor: pointer;
}

.mainmenubtnactive {
    color: white;
}

.top_menu {
    display: flex;
    gap: 2vw;
}

.top_name {
    display: flex;
    font-size: 21px;
    gap: 20px;
}

.footer {
    padding: 0 20px;
    width: 100%;
    background-color: #243235;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    height: $topmenuheight;
    font-size: 12px;
    color: $darkgrey;
}




// Menu

.langmenufake {
    // width: calc(20% + 20px);
    width: 15vw;
    // min-width: 220px;
    padding: 30px 20px;
    text-align: left;
}

.langmenu {
    // position: fixed;
    position: sticky;
    top: $topmenuheight;
    width: 20%;
    min-width: 160px;
    // width: 210px;
    // display: flex;
    // flex-direction: column;
    padding: 5px 20px;
    margin: 10px 0;
    text-align: left;
    // background: $main-bggrey;
    overflow-y: scroll;
    overflow-x: hidden;
    height: calc(100vh - 50px);

    &::-webkit-scrollbar {
        width: 12px;
    }

    &::-webkit-scrollbar-track {
        background: white;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $main-bggrey;
        border-radius: 20px;
        border: 3px solid white;
    }
}

.langlistbutton {
    width: fit-content;
    margin: 4px 8px 4px 0;
    text-decoration: none;
    white-space: nowrap;
    padding: 5px 7px;
    font-size: 14px;
    text-align: left;
    color: $darkgrey;
    background: none;
    cursor: pointer;
    // border: none;
    // border-radius: 20px;
    border: 1px solid $main-bggrey;

    &:hover {
        color: black;
        background: $main-grey;
        border: 1px solid $main-grey;
    }
}

.langlistbuttonactive {
    color: white;
    background: $darkgrey;
    border: 1px solid $darkgrey;
}

.langtitle {
    font-size: 64px;
    line-height: 70px;
    padding-top: 25px;
}

.langparams {
    width: 40%;
    // width: ($lettercube * 6 + 6px);
    margin: 0;
    padding: 35px 30px 20px 0;
    border-spacing: 0;
    font-size: 18px;
    line-height: 36px;
    height: fit-content;
}

.langlist {
    font-size: 18px;
    line-height: 27px;
    padding: 5px 15px 5px 0;
}

.tablenoborder {
    border: none;
}

.langlisttitle {
    color: $title;
    white-space: nowrap;
}

.langinfo {
    width: 60%;
    font-size: 18px;
    line-height: 27px;
    padding: 40px 40px 10px 0;
}



// Table

.tablecontainer {
    padding: 20px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 12px;
    }

    &::-webkit-scrollbar-track {
        background: white;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $main-grey;
        border-radius: 20px;
        border: 3px solid white;
    }
}

.tableheader {
    padding: 15px 20px 5px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tablewrap {
    display: flex;
    align-items: flex-start;
}

.tablestitle {
    font-size: 24px;
    line-height: 24px;
    padding: 20px 0 0 25px;
}

.tableinnertitle {
    font-size: 18px;
    line-height: 36px;
    padding-top: 15px;
    color: $title;
}

table {
    border-spacing: 25px 0;
    padding-bottom: 30px;
    // white-space: nowrap;
}

.tablerow {
    margin: 0 -25px -30px -25px;
}

th {
    text-align: left;
    width: 150px;
}

td {
    padding: 8px 0 8px 0;
    border-bottom: 1px solid $main-grey;
    vertical-align: top;
}

.letterchell {
    width: 70px;
}

.infochell {
    width: 220px;
}

.containerrow {
    border-bottom: none;
    padding: 0;
}




// Legend

.legendbgcontainer {
    position: sticky;
    top: 10px + $topmenuheight;
    z-index: 1000;
    // border-radius: 10px;
    width: fit-content;
}

.legendinfocontainer {
    display: flex;
}

.legendbg {
    display: flex;
    // flex-wrap: wrap;
    align-items: center;
    list-style-type: none;
    padding: 17px;
    margin: 20px 24px 0 0;
    // background: $main-bggrey;
    background: $grey-transparent;
    width: fit-content;
}

.legendinfoopener {
    padding: 17px;
    margin: 1px 24px 0 0;
    background: $main-bggrey;
}

.legenddiv {
    margin: 0 18px;
    flex-shrink: 0;
    border-width: 0;
    border-style: solid;
    border-color: white;
    border-bottom-width: 0;
    height: auto;
    border-right-width: thin;
    align-self: stretch;
}

.legenddivhoriz {
    margin: 18px 0;
    flex-shrink: 0;
    border-width: 0;
    border-style: solid;
    border-color: white;
    border-bottom-width: thin;
}

.legenbtnswrap {
    display: flex;
    flex-wrap: nowrap;
}

.legenbtns {
    display: flex;
    flex-wrap: nowrap;
}

.legenbtnsright {
    margin: 0 18px 0 0;
}

.legendtextwrap {
    // column-count: 3;
    // column-gap: 20px;
    display: flex;
    margin: 0;
    padding: 0 0 0 20px;
    list-style-type: none;
}

.legendinfowrap60 {
    width: 50%;
    padding: 10px 20px 10px 0;
    margin-top: 1px;
}

.legendinfowrap40 {
    width: 50%;
    padding: 10px 0 10px 20px;
    margin-top: 1px;
}

.legendinfotext {
    padding: 0 0 10px 0;
}

.legengtitle {
    color: $title;
    padding-top: 25px;
}

.altmarkinfo {
    width: 50px;
    padding: 5px 0;
    // margin: 0 20px 0 0;
}

.altmarkinfotext {
    width: 270px;
}

.legenditem {
    display: flex;
    padding: 3px 20px 3px 0;
}

.legendtext {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    margin-left: 10px;
    white-space: nowrap;
}



// Togglebuttons

.togglebtns {
    align-self: flex-end;
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    background-color: white;
    border: none;
    padding: 6px 10px;
    margin: 5px 0;
    cursor: pointer;

    &:hover {
        background-color: $darkgrey;
        color: white;
    }
}

.togglebtnsactive {
    background-color: $darkgrey;
    color: white;
    cursor: default;
}



// Letters

.lettersblock {
    padding: 0 0 50px 0;
}

.smalltitle {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    padding: 35px 15px 15px 0;
}

.lettersshowtext {
    font-size: 16px;
    color: $darkgrey;
    background: transparent;
    border: none;
    // text-transform: uppercase;
    cursor: pointer;
    padding: 0 0 50px 0;
    // margin: 0 0 0 32px;
    // outline: 1px solid $main-grey-equ;
}

.altborder {
    outline: 1px solid $darkgrey;
    outline-offset: -1px;
    display: flex;
    flex-wrap: nowrap;
}

.altborder_in {
    outline: 1px solid white;
    outline-offset: -2px;
    display: flex;
    flex-wrap: nowrap;
    gap: 1px;
}

.smallunititle {
    position: absolute;
    font-size: 10px;
    width: $lettercube;
    padding: 3px;
}

.letter {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    width: $lettercube;
    height: $lettercube;
    background: $main-grey;

    // &:hover {
    //     position: relative;
    //     opacity: 0.9;
    //     filter: alpha(opacity=90);
    // }
}

.bigletter {
    // font-family: 'Octava', 'PT Sans', sans-serif;
    font-family: 'PTSansExpert', sans-serif;
    font-size: 30px;
    line-height: 32px;
    padding-top: 21px;
    // padding: 23px 0 -5px 0;
}

.markplace {
    text-align: right;
    // padding: 0 5px 10px 10px;
    margin: 1px 4px 1px 5px;
}

.altmark {
    width: 32px;
    text-align: center;
    font-size: 9px;
    padding: 0 4px 1px 4px;
    border-radius: 5px;
    color: $grey-txt;
    background: $white-transparent;
}

.bigletterserifitalic {
    font-family: 'PTSerifExpertItalic', sans-serif;
}

.bigletteritalic {
    font-family: 'PTSansExpertItalic', sans-serif;
}

.bigletterserif {
    font-family: 'PTSerifExpert', sans-serif;
}

.selectedsign {
    background: $main-grey;
}

.unselectedsign {
    background: $main-bggrey;
}

.selectedletter {
    color: black;
}

.unselectedletter {
    color: $grey-txt;
}

.unselecteduni {
    color: $grey-txt;
}

// Letter backgrounds
// $main-yellow: #E6E183;
// $main-green: #9DD6BE;
// $main-red: #EEB4A7;
// $main-blue: #8FBCFF;

.dialect {
    background: #9DD6BE;
}

.historic {
    background: #E7B5B4;
}

.extended {
    background: #A8B9F2;
}

.digraph {
    background: #DEDA8D;
}

.consideration {
    background: #A4DFDD;
}

.foreign {
    background: #F6C8A1;
}

.repsign {
    background: $main-bggrey;
    color: $grey-txt;
}

.featuresign {
    font-feature-settings: "aalt";
    -moz-font-feature-settings: "aalt";
    -webkit-font-feature-settings: "aalt";
}



// Media

@media (max-width:1240px) {

    // .langlist {
    //     width: ($lettercube * 3);
    // }

    .langlisttitle {
        white-space: normal;
    }

    .legendtextwrap {
        // column-count: 1;
        // column-gap: 0;
        display: block;
        padding: 0 20px;
    }

}

@media (max-width:950px) {

    .innercontainer {
        flex-direction: column;
        // flex-direction: column-reverse;
    }

    // .langlist {
    //     width: 100%;
    // }
    .langparams {
        width: 100%;
        padding-bottom: 0;
    }

    .langinfo {
        padding: 25px 0 10px 0;
        width: 100%;
    }

    .legenbtnswrap {
        flex-direction: column;
        width: 142px;
    }

    .legendbg {
        align-items: flex-start;
    }

    .legenddiv {
        display: none;
    }

    .legendinfocontainer {
        display: block;
    }

    .legendinfowrap60,
    .legendinfowrap40 {
        width: 100%;
        padding: 10px 0;
    }
}

